	module.exports = {"data":{"markdownRemark":{"html":"<h2>Median</h2>\n<h4>Definition:</h4>\n<p>The median is one of the measures of central tendency of a data set. To find the median, we arrange the observations in order from smallest to largest value. If there is an odd number of observations, the median is the middle value. If there is an even number of observations, the median is the (mean) average of the two middle values.</p>\n<h4>Example 1:</h4>\n<p>In the data set [3, 4, 8, 6, 34, 22, 5], to find the median we sort the set to [3, 4, 5, 6, 8, 22, 34]. The middle value i.e. 6 is the median.</p>\n<h4>xample 1:</h4>\n<p>In the data set [3, 4, 8, 6, 22, 5], to find the median we sort the set to [3, 4, 5, 6, 8, 22]. The average of 5 and 6 i.e. 5.5 is the median.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p>More detailed and rigorous review can be found <a href=\"https://en.wikipedia.org/wiki/Median\">here</a></p>","fields":{"slug":"/mathematics/statistics/median/"},"frontmatter":{"title":"Median"}}},"pathContext":{"slug":"/mathematics/statistics/median/"}}

